import React from 'react';
import {TextField} from "@material-ui/core";
import {Autocomplete } from '@material-ui/lab';
import {useDispatch, useSelector} from "react-redux";
import {
    getApplicantsAsync,
    getClientsAsync, getFilterApplicantAsync,
    get–°lientAction
} from "../../store/actions";
import Submit from "../submitButtom/Submit";
import {makeStyles} from "@material-ui/styles";


const useStyles = makeStyles({
    client : {
        '& .name' : {
            margin: 0,
            marginBottom: '5px',
            fontSize: '17px',
        },

        '& .phone' : {
            marginBottom: '15px',
        }
    }
});

const Client = () => {

    // –§–∏–ª—å—Ç—Ä–∞—Ü–∏—è –º–∞—Å—Å–∏–≤–∞ –ø–æ—Å–ª–µ 3 —Å–∏–º–≤–æ–ª–∞
    const [filterClient, setFilter] = React.useState([])

    // –ü–æ–ª—É—á–∞–µ–º –æ–±—å–µ–∫—Ç –≤—ã–±—Ä–∞–Ω–æ–≥–æ –∫–ª–∏–µ–Ω—Ç–∞
    const client = useSelector( ({ client }) => client)

    // –ü–æ–ª—É—á–∞–µ–º –∫–ª–∏–µ–Ω—Ç–∞
    const clients = useSelector( ({ getClients }) => getClients || [])
    const dispatch = useDispatch()
    const styles = useStyles()

    // –ü–æ–ª—É—á–∞–µ–º –∞—Å–∏–Ω—Ö—Ä–æ–Ω–Ω–æ –∫–ª–∏–µ–Ω—Ç–∞
    React.useEffect( () => {
        dispatch(getClientsAsync(setFilter))
    }, [])

    // –ï—Å–ª–∏ —Å–∏–º–≤–æ–ª–æ–≤ –≤ –∏–Ω–ø—É—Ç–µ –±–æ–ª—å—à–µ 3, –±–µ—Ä–µ–º –∫–ª–∏–µ–Ω—Ç–æ–≤ –∫–æ—Ç–æ—Ä—ã—Ö –ø–æ–ª—É—á–∏–ª–∏ —Å —Å–µ—Ä–≤–µ—Ä–∞
    const handleInputChange = ( _, value) => {

        const valTrim = value.trim()

        if (valTrim.length > 3) {
            dispatch(getFilterApplicantAsync(value, setFilter))
        }
        else if ( valTrim === '' ) {
            setFilter(clients)
        }
        else {
            setFilter([])
        }
    }

    // –ï—Å–ª–∏ –≤ –∏–Ω–ø—É—Ç–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å, –ø–æ–ª—É—á–∞–µ–º –µ–≥–æ –∞—Å–∏–Ω—Ö—Ä–æ–Ω–Ω–æ
    // –∏–Ω–∞—á–µ null
    const handleChange = (event, client) => {
        dispatch(get–°lientAction(null))
        if (client) dispatch(getApplicantsAsync(client))
    }

    return (
        <div className={`wrap ${styles.client}`}>
            <h2>–ö–ªi—î–Ω—Ç</h2>

            <div className="select">

                <Autocomplete
                   className="select"
                   options={filterClient}
                   getOptionLabel={(option) => option?.name || option?.label}
                   onInputChange={handleInputChange}
                   onChange={handleChange}
                   loading={ clients.length ? false : true }
                   renderInput={
                       (params) =>
                       <TextField
                           {...params}
                           label="–ü–æ–∏—Å–∫ / –í—ã–±–æ—Ä –∫–ª–∏–µ–Ω—Ç–∞"
                           margin="normal"
                       />
                   }
                />

            </div>

            { client && <>
                          <h3 className="name">{ client?.name || client?.label }</h3>
                          <p className="phone">–¢–µ–ª–µ—Ñ–æ–Ω. { client?.phone || '–ù–µ —É–∫–∞–∑–∞–Ω' }</p>
                          <Submit />
                        </>
            }

        </div>
    );
}

export default Client;
