import React from 'react';
import {Checkbox, FormControlLabel} from "@material-ui/core";
import {useDispatch, useSelector} from "react-redux";
import AddApplicant from "./components/AddApplicant";
import {arrayIndexApplicantAction, deleteApplicant} from "../../store/actions";
import Submit from "../submitButtom/Submit";
import {makeStyles} from "@material-ui/styles";


const useStyles = makeStyles({
    root: {
        display : 'flex',
        marginBottom : '20px',
        ['@media (min-width:768px)']: {
            marginBottom : '0'
        }
    },
    title : {
        marginBottom : '15px'
    },
    wrapCheckBox : {
        marginBottom : '20px'
    },
    buttonWrap : {
        display : 'flex',
        flexDirection: 'column',
        width: '180px',
    },
});

// –ú–∞—Å—Å–∏–≤ –∏–Ω–¥–µ–∫—Å–æ–≤ —Å –∑–∞—è–≤–∏—Ç–µ–ª—è–º–∏
const checkIndex = []

// –ö–æ–º–ø–æ–Ω–µ–Ω—Ç –∑–∞—è–≤–∏—Ç–µ–ª–µ–π
const Applicants = () => {

    // –ü–æ–ª—É—á–µ–Ω–∏—è –æ–±—å–µ–∫—Ç–∞ –≤—ã–±—Ä–æ–Ω–æ–≥–æ –∫–ª–∏–µ–Ω—Ç–∞
    const client = useSelector(({client}) => client)
    // –ü–æ–ª—É—á–µ–Ω–∏–µ –º–∞—Å—Å–∏–≤ –∑–∞—è–≤–∏—Ç–µ–ª–µ–π
    const applicants = useSelector(({getApplicants}) => getApplicants || [])
    const dispatch = useDispatch()
    const styles = useStyles()

    // –£–¥–∞–ª–µ–Ω–∏–µ –∑–∞—è–≤–∏—Ç–µ–ª—è
    const handleDelete = (id, index) => {
        // –£–¥–∞–ª–µ–Ω–∏–µ —Å –º–∞—Å—Å–∏–≤–∞ –∏–Ω–¥–µ–∫—Å–æ–≤
        const elemIndex = checkIndex.indexOf(index)
        checkIndex.splice(elemIndex, 1)

        dispatch(arrayIndexApplicantAction(checkIndex))
        dispatch(deleteApplicant(id))
    }

    // –í—ã–±–æ—Ä –∑–∞—è–≤–∏—Ç–µ–ª—è, –µ—Å–ª–∏ –µ—Å—Ç—å —É–¥–∞–ª—è–µ–º, –µ—Å–ª–∏ –Ω–µ—Ç –ø—É—à–∏–º –≤ –º–∞—Å—Å–∏–≤
    const handleCheck = (e, index) => {
        if (!checkIndex.includes(index)) {
            checkIndex.push(index)
        }
        else {
            const elemIndex = checkIndex.indexOf(index)
            checkIndex.splice(elemIndex, 1)
        }
        dispatch(arrayIndexApplicantAction(checkIndex))
    }

    return (
        <div className="wrap">
            <h2 className={styles.title}>–ó–∞—è–≤–Ω–∏–∫–∏</h2>

            <div className={styles.wrapCheckBox}>
                { applicants.map( (applicant, index) => {
                    return (
                        <FormControlLabel
                            key={applicant.id}
                            className={styles.root}
                            control={
                                <Checkbox
                                    onChange={e => handleCheck(e, index)}
                                    name="checkedB"
                                    color="primary"
                                />
                            }
                            label={
                                <>
                                    <strong>{applicant.name}</strong>,
                                    {applicant.address.address}
                                    <span
                                        onClick={_ => handleDelete(applicant.id, index)}
                                        style={{
                                            display: applicant.delete
                                                ? 'inline-block'
                                                : 'none',
                                            marginLeft: '5px'
                                        }}
                                    >
                                       &#x2716;
                                    </span>
                                </>
                            }
                        />
                    )
                })}
            </div>

            <AddApplicant/>

            <div style={{ display : client ? 'block' : 'none'}}
                 className={styles.buttonWrap}
            >
                <Submit />
            </div>

        </div>
    );
};

export default Applicants;
