import {
    addApplicantData, arrayIndexApplicant,
    client,
    delApplicant,
    getApplicants,
    getClients, jsonDATA
} from "./constants";


const initialState = {
    getClients: [],
    getApplicants : [],
    client : null,
    data : {
        id : '',
        applicantsIds : [],
        newApplicants : []
    },
    jsonDATA : ''
}

export const reducer = (state = initialState, action) => {

    switch (action.type) {

        case getClients :

            return {
                ...state,
                getClients : action.clients
            }

        case client :

            return {
                ...state,
                client : action.clientObj
            }

        case getApplicants :

            return {
                ...state,
                getApplicants : action.applicants
            }

        case addApplicantData :

            return {
                ...state,
                getApplicants : [...state.getApplicants, action.data]
            }

        case delApplicant :

            return {
                ...state,
                getApplicants : state.getApplicants
                                .filter(applicant => applicant.id !== action.id)
            }

        // –§–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏–µ –æ–±—å–∫—Ç–∞ Json
        case arrayIndexApplicant :

            // –ü–æ–ª—É—á–∞–µ–º –≤—Å–µ—Ö –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π –ø–æ –∏–Ω–¥–µ–∫—Å–∞–º
            const applicantsIds = action.data.reduce((acc, itemIndex) => {
                acc.push(state.getApplicants[itemIndex])
                return acc
            }, [])

            // –ï—Å–ª–∏ —É –∑–∞—è–≤–∏—Ç–µ–ª—è –µ—Å—Ç—å —Å–≤–æ–π—Å—Ç–≤–æ delete —Ç–æ –¥–æ–±–∞–≤–ª–µ–º –≤ –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤
            const newApplicants = applicantsIds.filter(item => item?.delete)

            return {
                ...state,
                data : {
                    id : state.client?.id,
                    applicantsIds,
                    newApplicants
                }
            }

        case jsonDATA :

            return {
                ...state,
                jsonDATA: action.data
            }

        default :
            return {
                ...state
            }
    }
}
