import React from 'react';
import {Button, FormControlLabel, Radio, RadioGroup, TextField} from "@material-ui/core";
import {Autocomplete} from "@material-ui/lab";
import {useDispatch} from "react-redux";
import {addApplicant} from "../../../store/actions";
import {makeStyles} from "@material-ui/styles";


const useStyles = makeStyles({
    title : {
        marginBottom : '15px'
    },
    formAddApplicant : {
        width: '100%',
        marginBottom: '25px',
        '& .title' : {
            marginBottom : '15px',
            color: '#858585',
            fontSize: '18px'
        }
    },
    buttonAdd : {
        '& span' : {
            display : 'inline-block',
        }
    },
    buttonWrap : {
        display : 'flex',
        flexDirection: 'column',
        width: '180px',
        marginBottom : '15px',
    },
    formInner : {
        display: 'flex',
        flexDirection: 'column',
        '@media (min-width : 768px)' : {
            flexDirection: 'row',
            alignItems: 'flex-end'
        },

        '& .form-inputs-group' : {
            display: 'flex',
            flexDirection: 'column',
            '&:last-child' : {
                marginRight: '0',
            },
            '@media (min-width : 768px)': {
                marginRight: '20px',
                width: '50%',
            }
        },

        '& .label' : {
            marginBottom : '20px'
        }
    }
});

const countries = ['–£–∫—Ä–∞–∏–Ω–∞', '–ì—Ä—É–∑–∏—è', '–ò—Ç–∞–ª–∏—è']

const AddApplicant = () => {

    const [country, setCountry] = React.useState(countries[0])
    const [innCode, setInnCode] = React.useState('')
    const [name, setName] = React.useState('')
    const [address, setAddress] = React.useState('')
    const [city, setCity] = React.useState('')
    const [postCode, setPostCode] = React.useState('')
    const [originalName, setOriginalName] = React.useState('')
    const [originalAddress, setOriginalAddress] = React.useState('')
    const [openForm, setOpenForm] = React.useState(false)
    const dispatch = useDispatch()
    const styles = useStyles()

    // –°–º–µ–Ω–∞ —Å—Ç—Ä–∞–Ω—ã
    const handleChangeCountries = (_, value) => setCountry(value)

    // –û—Ç–∫—Ä—ã—Ç–∏–µ —Ñ–æ—Ä–º—ã –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ –∑–∞—è–≤–∏—Ç–µ–ª—è
    const handleClickOpenForm = () => setOpenForm(!openForm)

    // –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –∑–∞—è–≤–∏—Ç–µ–ª—è
    const handleAddApplicant = () => {

        if (name.length && address.length) {

            // –§–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏–µ –æ–±—å–µ–∫—Ç–∞
            const data = {
                address: {address, city, country, postCode},
                id: Date.now(),
                innCode,
                name,
                originalAddress,
                originalName,
                delete: true
            }

            // –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –∏ –æ—á–∏—Å—Ç–∫–∞ –∏–Ω–ø—É—Ç–æ–≤, –∑–∞–∫—Ä—ã—Ç–∏–µ —Ñ–æ—Ä–º—ã
            dispatch(addApplicant(data))
            setName('')
            setInnCode('')
            setAddress('')
            setCity('')
            setPostCode('')
            setOriginalName('')
            setOriginalAddress('')
            setOpenForm(false)
        }
    }

    return (<>
            <div className={styles.buttonWrap}>
                <Button
                    onClick={handleClickOpenForm}
                    className={styles.buttonAdd}
                    variant="outlined"
                    color="primary"
                >
                    –î–æ–¥–∞—Ç–∏ <span>+</span>
                </Button>
            </div>

            <form style={{ display: openForm ? 'block' : 'none'}}
                  className={styles.formAddApplicant}
            >
                <h4 className='title'>–î–æ–¥–∞—Ç–∏ –∑–∞—è–≤–Ω–∏–∫–∞</h4>

                <RadioGroup
                    aria-label="gender"
                    defaultValue="–§i–∑–∏—á–Ω–∞ –æ—Å–æ–±–∞"
                    name="gender1"
                    style={{marginBottom: '20px'}}
                >
                    <FormControlLabel value="–§i–∑–∏—á–Ω–∞ –æ—Å–æ–±–∞" control={<Radio color="primary"/>} label="–§i–∑–∏—á–Ω–∞ –æ—Å–æ–±–∞"/>
                    <FormControlLabel value="–Æ—Ä–∏–¥–∏—á–Ω–∞ –æ—Å–æ–±–∞" control={<Radio color="primary"/>} label="–Æ—Ä–∏–¥–∏—á–Ω–∞ –æ—Å–æ–±–∞"/>
                </RadioGroup>

                <div className={styles.formInner}>
                    <div className="form-inputs-group">
                        <div className="select">
                            <Autocomplete
                                className="select"
                                options={countries}
                                getOptionLabel={(option) => option}
                                onChange={handleChangeCountries}
                                defaultValue={countries[0]}
                                renderInput={
                                    (params) =>
                                        <TextField
                                            {...params}
                                            label="–ö—Ä–∞—ó–Ω–∞"
                                            margin="normal"
                                        />
                                }
                            />
                        </div>

                        <TextField value={innCode} onChange={e => setInnCode(e.target.value)}
                                   style={{marginBottom: '20px'}} label="USREOU"/>
                        <TextField value={name} onChange={e => setName(e.target.value)} style={{marginBottom: '20px'}}
                                   label="I–º'—è"/>
                        <TextField value={address} onChange={e => setAddress(e.target.value)}
                                   style={{marginBottom: '20px'}} label="–ê–¥—Ä–µ—Å–∞"/>
                        <TextField value={city} onChange={e => setCity(e.target.value)} style={{marginBottom: '20px'}}
                                   label="–úi—Å—Ç–æ"/>
                        <TextField value={postCode} onChange={e => setPostCode(e.target.value)}
                                   style={{marginBottom: '20px'}} label="I–Ω–¥–µ–∫—Å"/>
                    </div>

                    <div className="form-inputs-group"
                        style={{
                            display: country === '–£–∫—Ä–∞–∏–Ω–∞'
                                     ? 'none'
                                     : 'inline-flex'
                        }}
                    >

                        <TextField value={originalName} onChange={e => setOriginalName(e.target.value)}
                                   style={{marginBottom: '20px'}} label="–ù–∞–∑–≤–∞ –º–æ–≤–æ—é –ø–æ—Ö–æ–¥–∂–µ–Ω–Ω—è"/>
                        <TextField value={originalAddress} onChange={e => setOriginalAddress(e.target.value)}
                                   style={{marginBottom: '20px'}} label="–ê–¥—Ä–µ—Å–∞ –º–æ–≤–æ—é –ø–æ—Ö–æ–¥–∂–µ–Ω–Ω—è"/>
                    </div>
                </div>

                <div className={styles.buttonWrap}>
                    <Button
                        onClick={handleAddApplicant}
                        className={styles.buttonAdd}
                        variant="outlined"
                        color="primary"
                    >
                        –î–æ–¥–∞—Ç–∏
                    </Button>
                </div>

            </form>
        </>
    );
};

export default AddApplicant;
