import React from 'react';
import {Button} from "@material-ui/core";
import {useDispatch, useSelector} from "react-redux";
import {jsonDataAction} from "../../store/actions";

const Submit = () => {

    // –ü–æ–ª—É—á–∞–µ–º –æ–±—å–µ–∫—Ç —Å –≤—ã–±—Ä–∞–Ω—ã–º–∏ –¥–∞–Ω–Ω—ã–º–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
    const data = useSelector( ({ data }) => data)
    const dispatch = useDispatch()

    // –ü—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ –ó–ë–ï–†–ï–ì–¢–ò –ó–ê–Ø–í–ö–£ –≤—ã–≤–æ–¥–∏–º Json —Å—Ç—Ä–æ–∫—É.
    const handleClick = () => {
        dispatch(jsonDataAction(JSON.stringify(data, null, 2)))
    }

    return (<>
        <Button
            onClick={handleClick}
            style={{fontSize : '15px'}}
            variant="contained"
            color="primary"
        >
            –ó–ë–ï–†–ï–ì–¢–ò –ó–ê–Ø–í–ö–£
        </Button>
    </>);
};

export default Submit;
